/* Container that enables horizontal scroll */
.table-container {
  width: 100%;
  max-height: 400px; /* Set desired max height */
  overflow-x: auto;
  overflow-y: auto; /* Enable vertical scroll */
  -webkit-overflow-scrolling: touch;
  margin-bottom: 1rem;
}

/* Force table to maintain column layout */
.user-table {
  width: 100%;
  min-width: 900px;
  border-collapse: collapse;
  background-color: white;
  table-layout: auto;
}

/* Cell styles */
.user-table th,
.user-table td {
  padding: 12px 16px;
  text-align: left;
  border-bottom: 1px solid #e5e7eb;
  white-space: nowrap; /* Prevent wrapping */
}

/* Hover effect */
.user-table tr:hover {
  background-color: #f9fafb;
}

/* Hide actions unless hovered (optional) */
.actions-cell {
  opacity: 0;
  transition: opacity 0.2s ease;
}
.user-table tr:hover .actions-cell {
  opacity: 1;
}

/* Smaller font on mobile (optional) */
@media (max-width: 768px) {
  .user-table {
    font-size: 14px;
  }
}
