.user-management-container { 
  font-family: 'Segoe UI', sans-serif;
}

.user-list-section {
  background-color: #fff;
  padding: 1rem;
  border-radius: 12px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.05);
}

.user-list-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
  flex-wrap: wrap;
}

.user-list-header h2 {
  font-size: 1.5rem;
  margin: 0.5rem 0;
}

.user-list-actions {
  display: flex;
  gap: 0.5rem;
  margin: 0.5rem 0;
}

.icon-button {
  background-color: #f2f2f2;
  border: none;
  padding: 0.5rem;
  border-radius: 8px;
  cursor: pointer;
  transition: background-color 0.2s ease;
}
.icon-button:hover {
  background-color: #e0e0e0;
}

.no-results {
  padding: 1rem;
  text-align: center;
  color: #777;
  font-style: italic;
}