/* Backdrop with blur glass effect */
.modal-backdrop {
  position: fixed;
  inset: 0;
  background: rgba(0, 0, 0, 0.4);
  backdrop-filter: blur(5px);
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 1rem;
  z-index: 1000;
  animation: fadeInBackdrop 0.4s ease-out forwards;
}

/* Modal Card */
.modal {
  background: rgba(7, 7, 7, 0.5);
  border-radius: 20px;
  padding: 2.2rem 2rem;
  width: 100%;
  max-width: 500px;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.25);
  backdrop-filter: blur(18px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  animation: popInModal 0.5s cubic-bezier(0.25, 1.25, 0.5, 1) forwards;
  opacity: 0;
  transform: scale(0.95);
  font-family: "Poppins", "Segoe UI", sans-serif;
  color: #ffffff;
}

/* Heading */
.modal h2 {
  font-size: 1.8rem;
  text-align: center;
  margin-bottom: 1.5rem;
  color: #ffffff;
  background: linear-gradient(to right, #00c6ff, #0072ff);
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
}

/* Form Layout */
.modal form {
  display: flex;
  flex-direction: column;
  gap: 1.2rem;
}

/* Inputs */
.modal input {
  padding: 0.8rem 1rem;
  border-radius: 12px;
  border: 1px solid rgba(255, 255, 255, 0.25);
  background: rgba(255, 255, 255, 0.1);
  font-size: 1rem;
  color: #fff;
  transition: all 0.3s ease;
}

.modal input::placeholder {
  color: rgba(255, 255, 255, 0.7);
}

.modal input:focus {
  outline: none;
  background: rgba(255, 255, 255, 0.2);
  border-color: #00c6ff;
  box-shadow: 0 0 6px rgba(0, 198, 255, 0.6);
}

/* Action buttons */
.modal-actions {
  display: flex;
  justify-content: flex-end;
  gap: 1rem;
  flex-wrap: wrap;
  margin-top: 1.5rem;
}

.modal-actions button {
  padding: 0.7rem 1.6rem;
  font-size: 1rem;
  border-radius: 10px;
  font-weight: 500;
  cursor: pointer;
  border: none;
  transition: all 0.3s ease;
}

/* Submit Button */
.modal-actions button[type="submit"] {
  background: linear-gradient(to right, #00c6ff, #0072ff);
  color: white;
  box-shadow: 0 4px 12px rgba(0, 198, 255, 0.4);
}

.modal-actions button[type="submit"]:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 16px rgba(0, 198, 255, 0.6);
}

/* Cancel Button */
.modal-actions button[type="button"] {
  background: rgba(255, 255, 255, 0.15);
  color: white;
  border: 1px solid rgba(255, 255, 255, 0.3);
}

.modal-actions button[type="button"]:hover {
  background: rgba(255, 255, 255, 0.25);
  transform: translateY(-2px);
}

/* Mobile Responsiveness */
@media (max-width: 480px) {
  .modal {
    padding: 1.5rem;
  }

  .modal-actions {
    flex-direction: column;
    align-items: stretch;
  }

  .modal-actions button {
    width: 100%;
  }
}

/* Animations */
@keyframes fadeInBackdrop {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes popInModal {
  from {
    transform: scale(0.9) translateY(20px);
    opacity: 0;
  }
  to {
    transform: scale(1) translateY(0);
    opacity: 1;
  }
}
