/* UserActions.css */

.user-list-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
  gap: 1rem;
  margin-bottom: 1rem;
}

/* All Users heading */
.user-list-header h2 {
  font-size: 1.4rem;
  font-weight: 600;
  margin: 0;
  white-space: nowrap;
  flex-shrink: 0;
}

/* Container for search + actions */
.search-and-actions {
  display: flex;
  align-items: center;
  gap: 1rem;
  flex: 1 1 auto;
  min-width: 240px;
  max-width: 100%;
  flex-wrap: nowrap;
  justify-content: flex-end;
}

/* Make sure the search box doesn’t expand too much */
.search-box {
  margin-right: 1rem;
  flex: 0 1 250px;
  max-width: 100%;
}

/* Buttons container */
.user-list-actions {
  display: flex;
  gap: 0.75rem; /* increased space between buttons and search */
  flex-shrink: 0;
}

/* Buttons styling */
.icon-button {
  background-color: #f2f2f2;
  border: none;
  padding: 0.4rem 0.6rem;
  border-radius: 8px;
  cursor: pointer;
  transition: background-color 0.2s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #333;
}

.icon-button:hover {
  background-color: #e0e0e0;
}

.add-btn {
  color: #2d8cff;
}

.delete-btn {
  color: #ff4d4f;
}

/* 📱 Responsive layout for smaller screens */
@media (max-width: 600px) {
  .user-list-header {
    flex-direction: column;
    align-items: center;
  }

  .user-list-header h2 {
    text-align: center;
    margin-bottom: 0.5rem;
  }

  .search-and-actions {
    width: 90%;
    flex-direction: row;
    justify-content: space-between; 
  }

  .search-box {
    flex: 1 1 60%;
  }

  .user-list-actions {
    display: flex;
    justify-content: flex-end;
  }
}
