/* Variables for easier theme management */
:root {
  --primary-color: #007bff;
  --secondary-color: #0056b3;
  --accent-color: #f4b400; /* Google yellow */
  --text-color-dark: #222;
  --text-color-medium: #444;
  --border-color: #ccc;
  --background-light: #ffffff;
  --shadow-light: rgba(0, 0, 0, 0.08);
  --border-radius: 12px;
  --font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

/* General body styling for better integration */
body {
  margin: 0;
  padding: 0;
  font-family: var(--font-family);
  background-color: #f0f2f5; /* Light grey background for the page */
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

/* Container for the entire feedback form */
.feedback-form-container {
  max-width: 600px;
  margin: 4vh auto;
  padding: 2.5rem; /* Increased padding for more breathing room */
  background: var(--background-light);
  border-radius: var(--border-radius);
  box-shadow: 0 10px 30px var(--shadow-light); /* Slightly more pronounced shadow */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  flex-grow: 1; /* Allows container to grow and take available space */
  box-sizing: border-box;
}

.form-title {
  text-align: center;
  font-size: 1.75rem;
  font-weight: 600;
  color: var(--text-color-dark);
  margin-bottom: 2rem;
  letter-spacing: -0.3px;
  line-height: 1.4;
  background: linear-gradient(90deg, #000000, #605e5e);
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
}


/* Form layout */
.feedback-form {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 1.8rem; /* Increased gap between form elements */
}

/* Rating label and stars */
.rating-section {
  text-align: center;
  margin:auto
}

.rating-label {
  font-size: 1.2rem; /* Slightly larger label */
  font-weight: 600; /* Bolder label */
  margin-bottom: 0.8rem; /* More space between label and stars */
  color: var(--text-color-medium);
  display: block; /* Ensures label takes its own line */
}

.stars {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  gap: 0.5rem; /* Slightly increased gap between stars */
  width: 100%;
}

.star {
  position: relative;
  font-size: 3rem; /* Larger stars */
  color: var(--border-color); /* Unfilled star color */
  cursor: pointer;
  transition: transform 0.2s ease-in-out, color 0.2s ease-in-out; /* Added color transition */
  display: inline-block;
}

.star:hover {
  transform: translateY(-3px) scale(1.1); /* Slight lift and scale on hover */
  color: #a0a0a0; /* Darker grey on hover for unfilled part */
}

.star::before {
  content: "★";
  position: absolute;
  top: 0;
  left: 0;
  width: 0%;
  color: var(--accent-color); /* Google yellow */
  overflow: hidden;
  z-index: 1;
  transition: width 0.3s ease-in-out; /* Smooth fill transition */
}

.star[data-filled="1"]::before {
  width: 100%;
}

.star[data-filled="0.5"]::before {
  width: 50%;
}

.star[data-filled="0"]::before {
  width: 0%;
}


/* Feedback textarea */
.feedback-text {
  width: 100%;
  min-height: 140px; /* Slightly taller textarea */
  padding: 1.2rem; /* More padding */
  border: 1px solid var(--border-color);
  border-radius: var(--border-radius);
  resize: vertical;
  font-size: 1.05rem; /* Slightly larger font */
  line-height: 1.7; /* Better line spacing */
  font-family: inherit;
  box-sizing: border-box;
  transition: border-color 0.3s ease, box-shadow 0.3s ease;
}

.feedback-text::placeholder {
  color: #888; /* Softer placeholder text color */
}

.feedback-text:focus {
  border-color: var(--primary-color);
  outline: none;
  box-shadow: 0 0 0 4px rgba(0, 123, 255, 0.15); /* Slightly larger and more opaque shadow */
}

/* Submit button */
.submit-btn {
  background: linear-gradient(135deg, var(--primary-color), var(--secondary-color));
  color: #fff;
  border: none;
  padding: 1rem 2.5rem; /* Increased padding */
  border-radius: var(--border-radius);
  font-size: 1.15rem; /* Larger font size */
  font-weight: 700; /* Bolder text */
  cursor: pointer;
  align-self: center;
  transition: all 0.3s ease-in-out;
  box-shadow: 0 4px 10px rgba(0, 123, 255, 0.2); /* Subtle shadow for button */
}

.submit-btn:hover {
  background: linear-gradient(135deg, var(--secondary-color), #003d80);
  transform: translateY(-2px) scale(1.02); /* Lift and slight scale */
  box-shadow: 0 6px 15px rgba(0, 123, 255, 0.3); /* Enhanced shadow on hover */
}

.submit-btn:active {
  transform: translateY(0); /* Press effect */
  box-shadow: 0 2px 5px rgba(0, 123, 255, 0.2);
}

/* --- Responsive Adjustments --- */

/* Tablet adjustments */
@media (max-width: 768px) {
  .feedback-form-container {
    margin: 3vh 1.5rem;
    padding: 2rem; /* Adjusted padding */
  }

  .form-title {
    font-size: 2rem;
    margin-bottom: 2rem;
  }

  .star {
    font-size: 2.5rem; /* Adjusted star size */
  }

  .rating-label {
    font-size: 1.1rem;
  }

  .feedback-text {
    font-size: 1rem;
    padding: 1rem;
  }

  .submit-btn {
    font-size: 1rem;
    padding: 0.9rem 2rem;
  }
}

/* Mobile adjustments */
@media (max-width: 480px) {
  .feedback-form-container {
    margin: 2vh 1rem;
    padding: 1.5rem; /* Adjusted padding */
    border-radius: 10px; /* Slightly smaller border-radius for small screens */
  }

  .form-title {
    font-size: 1.8rem;
    margin-bottom: 1.8rem;
  }

  .rating-label {
    font-size: 1rem;
  }

  .stars {
    margin:auto;
    gap: 0.3rem; /* Tighter star spacing on small screens */
  }

  .star {
    font-size: 2.2rem; /* Adjusted star size */
  }

  .feedback-text {
    min-height: 120px;
    font-size: 0.95rem;
    padding: 0.9rem;
  }

  .submit-btn {
    font-size: 0.95rem;
    padding: 0.8rem 1.8rem;
    border-radius: 8px;
  }
}

/* Extra small mobile adjustments */
@media (max-width: 320px) {
  .feedback-form-container {
    margin: 1.5vh 0.8rem;
    padding: 1.2rem;
  }

  .form-title {
    font-size: 1.6rem;
  }
  .stars{
    margin: auto;
    padding: auto;
  }

  .star {
    font-size: 2rem;
  }

  .submit-btn {
    font-size: 0.85rem;
    padding: 0.7rem 1.5rem;
  }
}