.actions-cell {
  display: flex;
  gap: 0.5rem;
  opacity: 10;
  transition: opacity 0.2s ease;
}

tr:hover .actions-cell {
  opacity: 1;
}

.icon-button {
  cursor: pointer;
  background: none;
  border: none;
  padding: 0.25rem;
  display: inline-flex;
  align-items: center;
  justify-content: center;
}

.clickable-email {
  cursor: pointer;
  color: inherit;        
  font-weight: normal;      /* Normal weight */
  text-decoration: none;    /* No underline */
  background-color: transparent;
  padding: 0;               /* No padding */
  transition: background-color 0.2s ease;
  border: none;
}

.clickable-email:hover {
  background-color: rgba(0, 0, 0, 0.08); /* Subtle highlight on hover */
}
