/* Container layout */
.dashboard-container {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  color: #333;
  padding: 0;
  margin: 0;
  min-height: 100vh;
  background-color: #f4f4f8;
}

/* Header styling */
.dashboard-header {
  position: relative;
  color: white;
  text-align: center;
  height: 180px;
  border-bottom-left-radius: 8px;
  border-bottom-right-radius: 8px;
  overflow: hidden;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  font-weight: 600;
  letter-spacing: 1.2px;
  box-shadow: 0 5px 15px rgba(0,0,0,0.3);
}

/* Semi-transparent overlay */
.header-overlay {
  position: absolute;
  inset: 0;
  background-color: rgba(0, 0, 0, 0.35);
  border-bottom-left-radius: 8px;
  border-bottom-right-radius: 8px;
  z-index: 1;
}

/* Content on top of overlay */
.header-content {
  position: relative;
  z-index: 2;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 1rem;
  user-select: none;
}

.header-content h1 {
  font-size: 2.2rem;
  margin: 0 0 0.75rem 0;
  text-shadow: 1px 1px 6px rgba(0,0,0,0.75);
}

/* Navigation buttons */
.nav-button {
  margin: 0.5rem;
  padding: 0.5rem 1.5rem;
  background: rgba(255, 255, 255, 0.85);
  border: 2px solid #4a4a4a;
  border-radius: 25px;
  color: #333;
  font-weight: 700;
  cursor: pointer;
  transition: background 0.3s ease, color 0.3s ease, border-color 0.3s ease;
  box-shadow: 0 3px 8px rgba(0, 0, 0, 0.12);
  font-size: 1rem;
  user-select: none;
}

.nav-button:hover {
  background: #2c2c2c;
  color: #f0e7db;
  border-color: #1f1f1f;
}

/* Main content layout */
.dashboard-main {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
  padding: 2rem;
  align-items: center;
}

/* Cards with reduced border-radius and shadow */
.dashboard-card {
  background: linear-gradient(135deg, #dcd6f7 0%, #f3eafe 100%);
  border-radius: 8px;
  box-shadow: 0 8px 20px rgba(102, 93, 173, 0.3);
  padding: 1.5rem;
  cursor: pointer;
  transition: transform 0.25s ease, box-shadow 0.25s ease;
  color: #2e004f;
  width: 80%;
  max-width: 600px;
}

.dashboard-card:hover {
  transform: scale(1.03);
  box-shadow: 0 12px 30px rgba(102, 93, 173, 0.5);
}

.dashboard-card h2 {
  margin-bottom: 1rem;
  font-size: 1.7rem;
  font-weight: 700;
}

/* Secondary card styling */
.card-secondary {
  background: linear-gradient(135deg, #fff1cc 0%, #fff8e1 100%);
  box-shadow: 0 8px 20px rgba(255, 200, 90, 0.3);
  border-radius: 8px;
  color: #5b2c06;
  width: 80%;
  max-width: 600px;
}

.card-secondary .feedback-entry {
  background: linear-gradient(135deg, #fff3c9, #fff8e7);
  border-radius: 6px;
  box-shadow: 0 3px 10px rgba(255, 200, 90, 0.2);
  padding: 0.75rem 1rem;
  margin-bottom: 0.75rem;
  color: #5b2c06;
  transition: transform 0.2s ease, background-color 0.2s ease;
  border: 1px solid #f8e4b3;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 1rem;
}

.card-secondary .feedback-entry > span {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}


.card-secondary .feedback-entry:hover {
  transform: translateY(-2px);
  background-color: #fff4d8;
}

.card-secondary .no-data {
  text-align: center;
  color: #7a5c29;
  font-style: italic;
  padding: 1rem 0;
  opacity: 0.75;
}

.feedback-entry span {
  flex: 1;
  min-width: 0;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}


/* Responsive layout */
@media (min-width: 768px) {
  .dashboard-main {
    flex-direction: row;
    justify-content: center;
    gap: 2rem;
  }

  .dashboard-card {
    width: 45%;
    max-width: none;
  }
}

/* Mobile improvements */
@media (max-width: 767px) {
  .dashboard-header {
    height: 150px;
    border-bottom-left-radius: 8px;
    border-bottom-right-radius: 8px;
  }

  .header-content h1 {
    font-size: 1.6rem;
  }

  .nav-button {
    padding: 0.4rem 1rem;
    font-size: 0.9rem;
    margin: 0.3rem 0.3rem 1rem 0.3rem;
  }

  .dashboard-main {
    padding: 1rem;
    gap: 1rem;
  }

  .dashboard-card {
    width: 80%;
    padding: 1rem;
  }

  .dashboard-card h2 {
    font-size: 1.4rem;
  }
}

/* List inside dashboard card */
.dashboard-card ul {
  padding: 0;
  margin: 1rem 0 0;
  list-style: none;
  width: 100%;
}

.dashboard-card .clickable-email {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.75rem 1rem;
  margin-bottom: 0.75rem;
  background: linear-gradient(135deg, #ede7fb 0%, #f5f0ff 100%);
  border-radius: 6px;
  box-shadow: 0 3px 10px rgba(102, 93, 173, 0.15);
  color: #2e004f;
  cursor: pointer;
  transition: all 0.2s ease;
  border: 1px solid #d5cdea;
}

.dashboard-card .clickable-email:hover {
  transform: translateY(-1px);
  background-color: #f0e9fc;
  box-shadow: 0 6px 16px rgba(102, 93, 173, 0.25);
}

/* Actions inside list */
.dashboard-card .list-actions {
  display: flex;
  gap: 0.5rem;
  align-items: center;
  justify-content: flex-end;
}

/* Icon-style buttons */
.dashboard-card .icon-button {
  background: none;
  border: none;
  cursor: pointer;
  font-size: 1.1rem;
  color: #5a2a9c;
  transition: transform 0.2s ease, color 0.2s ease;
}

.dashboard-card .icon-button:hover {
  transform: scale(1.2);
  color: #2e004f;
}

/* No data message */
.dashboard-card .no-data {
  text-align: center;
  color: #5a5a5a;
  font-style: italic;
  padding: 1rem 0;
  opacity: 0.75;
}
