.employee-feedback-container {
  max-width: 700px;
  margin: 3rem auto;
  padding: 2rem 2.5rem;
  background: #f9faff;
  border-radius: 18px;
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.1);
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  color: #222;
}

.title {
  text-align: center;
  font-size: 2.2rem;
  font-weight: 700;
  margin-bottom: 2rem;
  color: #222;
}

.email-section {
  display: flex;
  justify-content: center;
  gap: 1rem;
  margin-bottom: 2.5rem;
}

.email-input {
  width: 300px;
  padding: 0.7rem 1rem;
  font-size: 1rem;
  border: 2px solid #ccc;
  border-radius: 10px;
  transition: border-color 0.3s ease;
}

.email-input:focus {
  outline: none;
  border-color: #4a90e2;
  box-shadow: 0 0 8px #4a90e2aa;
}

.load-users-btn {
  background-color: #4a90e2;
  color: white;
  border: none;
  padding: 0.7rem 1.5rem;
  border-radius: 10px;
  font-weight: 600;
  cursor: pointer;
  transition: background-color 0.25s ease;
}

.load-users-btn:hover:not(:disabled) {
  background-color: #357abd;
}

.load-users-btn:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

.users-rating-list {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.user-rating-row {
  background: white;
  padding: 1rem 1.5rem;
  border-radius: 14px;
  box-shadow: 0 4px 14px rgb(0 0 0 / 0.06);
  display: flex;
  justify-content: space-between; 
}

.user-name {
  font-weight: 600;
  font-size: 1.1rem;
  color: #333;
}

.stars {
  display: flex;
  gap: 0.25rem;
}

.star {
  font-size: 2.4rem;
  cursor: pointer;
  user-select: none;
  color: #ccc; /* default grey */
  position: relative;
  display: inline-block;
  transition: color 0.2s ease;
}

.star[data-filled="1"] {
  color: #f5a623; /* full star */
}

.star[data-filled="0.5"] {
  color: #ccc; /* base color for half star */
}

.star[data-filled="0.5"]::before {
  content: "★";
  position: absolute;
  left: 0;
  top: 0;
  width: 50%;
  overflow: hidden;
  color: #f5a623;
  pointer-events: none;
}

.star:hover {
  color: orange !important;
}




.submit-btn {
  margin: 2rem auto 0 auto;
  display: block;
  background-color: #28a745;
  color: white;
  font-weight: 700;
  font-size: 1.2rem;
  padding: 0.85rem 3rem;
  border-radius: 12px;
  border: none;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.submit-btn:hover:not(:disabled) {
  background-color: #1e7e34;
}

.submit-btn:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

/* Responsive */
@media (max-width: 600px) {
  .employee-feedback-container {
    margin: 1.5rem;
    padding: 1.5rem;
  }

  .email-section {
    flex-direction: column;
    align-items: center;
  }

  .email-input {
    width: 100%;
    max-width: 300px;
  }

  .user-rating-row {
    flex-direction: column;
    gap: 0.7rem;
    align-items: flex-start;
  }

  .stars {
    justify-content: flex-start;
  }

  .star {
    font-size: 2rem;
  }

  .submit-btn {
    width: 100%;
  }
}
.star.empty {
  color: #ccc; /* truly grey unless rated */
}