.qr-modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 999;
}

.qr-modal-content {
  background: #fff;
  padding: 30px;
  border-radius: 16px;
  position: relative;
  width: 90%;
  max-width: 420px;
  text-align: center;
  box-shadow: 0 8px 24px rgba(0, 0, 0, 0.2);
}

.close-btn {
  position: absolute;
  top: 12px;
  right: 12px;
  background: none;
  border: none;
  cursor: pointer;
}

.qr-image {
  width: 200px;
  height: 200px;
  margin-bottom: 20px;
  border-radius: 10px;
  object-fit: contain;
}

.qr-btn-group {
  display: flex;
  justify-content: center;
  gap: 16px;
  flex-wrap: wrap;
  margin-top: 10px;
}

/* Shared button style */
.print-btn,
.um-update-btn {
  padding: 12px 20px;
  font-size: 1rem;
  font-weight: 600;
  color: #fff;
  background: linear-gradient(135deg, #6c63ff, #5a55f5);
  border: none;
  border-radius: 10px;
  cursor: pointer;
  box-shadow: 0 4px 12px rgba(108, 99, 255, 0.3);
  transition: all 0.3s ease-in-out;
  min-width: 140px;
}

/* Hover effect */
.print-btn:hover,
.um-update-btn:hover {
  background: linear-gradient(135deg, #5848f5, #4435f2);
  transform: translateY(-2px);
  box-shadow: 0 6px 16px rgba(90, 85, 245, 0.4);
}

/* Active click effect */
.print-btn:active,
.um-update-btn:active {
  transform: scale(0.98);
  box-shadow: 0 2px 6px rgba(90, 85, 245, 0.25);
}


/* Print-only style */
@media print {
  body * {
    visibility: hidden;
  }

  .print-area,
  .print-area * {
    visibility: visible;
  }

  .print-area {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
  }
}

/* Animations */
@keyframes fadeIn {
  from {
    background-color: rgba(0, 0, 0, 0);
  }
  to {
    background-color: rgba(0, 0, 0, 0.5);
  }
}

@keyframes scaleIn {
  from {
    transform: scale(0.9);
    opacity: 0;
  }
  to {
    transform: scale(1);
    opacity: 1;
  }
}
