/* SearchBox.css */

.search-box {
  flex: 0 1 250px;
  max-width: 100%;
}

.search-box input {
  width: 100%;
  padding: 0.5rem 0.75rem;
  border: 1px solid #ccc;
  border-radius: 8px;
  font-size: 1rem;
  transition: border-color 0.3s ease;
}

.search-box input:focus {
  border-color: #4a90e2;
  outline: none;
}
