.feedback-page-container {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  background: linear-gradient(to right, #e3f2fd, #ffffff);
  font-family: 'Inter', sans-serif;
}

.feedback-main {
  padding: 2rem;
  flex: 1;
}

.feedback-main h2 {
  text-align: center;
  font-size: 2rem;
  font-weight: 600;
  margin-bottom: 2.5rem;
  color: #2c3e50;
  letter-spacing: 0.5px;
}

.search-box-wrapper {
  display: flex;
  justify-content: center;
  margin-bottom: 2rem;
}

.search-box-wrapper .search-box input {
  padding: 12px 16px;
  font-size: 16px;
  border: 1px solid #ddd;
  border-radius: 12px;
  width: 100%;
  max-width: 340px;
  background-color: #ffffff;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.05);
  transition: border-color 0.3s ease, box-shadow 0.3s ease;
}

.search-box-wrapper .search-box input:focus {
  outline: none;
  border-color: #4a90e2;
  box-shadow: 0 0 8px rgba(74, 144, 226, 0.3);
}

.employee-list {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 2rem;
  padding: 0 1rem;
}

.employee-card {
  backdrop-filter: blur(10px);
  background: rgba(255, 255, 255, 0.6);
  border-radius: 16px;
  padding: 1.8rem;
  box-shadow: 0 12px 24px rgba(0, 0, 0, 0.08);
  transition: all 0.25s ease-in-out;
  position: relative;
  overflow: hidden;
}

.employee-card::before {
  content: "";
  position: absolute;
  top: -20%;
  right: -20%;
  width: 120%;
  height: 120%;
  background: linear-gradient(135deg, #4a90e2, #76b2fe);
  opacity: 0.05;
  transform: rotate(45deg);
}

.employee-card:hover {
  transform: translateY(-6px);
  box-shadow: 0 16px 32px rgba(0, 0, 0, 0.12);
}

.employee-card h3 {
  font-size: 1.4rem;
  font-weight: 600;
  color: #2c3e50;
  margin-bottom: 0.75rem;
}

.employee-card p {
  font-size: 1rem;
  margin: 0.4rem 0;
  color: #4b4b4b;
}

.employee-card p strong {
  color: #2b2b2b;
}

.employee-card .tag {
  display: inline-block;
  padding: 4px 10px;
  font-size: 0.75rem;
  font-weight: 500;
  border-radius: 8px;
  margin-right: 8px;
  margin-top: 8px;
}

.employee-card .tag.employee {
  background-color: #d0e7ff;
  color: #0062cc;
}

.employee-card .tag.customer {
  background-color: #ffe0b3;
  color: #e67e22;
}

.no-data {
  text-align: center;
  font-size: 1.2rem;
  color: #7f8c8d;
  padding: 2rem;
}

.employee-card.clickable {
  cursor: pointer;
}

/* Mobile responsiveness */
@media (max-width: 600px) {
  .feedback-main {
    padding: 1rem;
  }

  .employee-card {
    padding: 1.2rem;
  }
}
