/* Header.css */

.modern-header {
  background-size: cover;
  background-position: center; /* Default for desktop */
  height: 220px;
  border-bottom-left-radius: 0.8rem;
  border-bottom-right-radius: 0.8rem;
  overflow: hidden;
  position: relative;
  box-shadow: 0 4px 10px rgba(255, 255, 255, 0.2);
  display: flex;
  align-items: center;
  justify-content: center;
}

.overlay {
  position: absolute;
  top: 0;
  left: 0;
  /* --- CHANGE 1: Reduce opacity of the main header overlay --- */
  background-color: rgba(0, 0, 0, 0.2); /* Changed from 0.32 to 0.2 */
  width: 100%;
  height: 100%;
  padding: 0 1.5rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: white;
  text-align: center;
  z-index: 2;
}

/* Existing brand-logo and brand-title styles (if you add them later) */
.brand-logo {
  height: 100px;
  max-width: 300px;
  object-fit: contain;
  margin: 0 auto 0.6rem auto;
  filter: drop-shadow(1px 1px 4px rgba(0, 0, 0, 0.4));
}

.brand-title {
  font-size: 1.8rem;
  font-weight: bold;
  margin-bottom: 0.4rem;
}

.nav-buttons {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 1rem;
  margin-top: 1rem;
}

.nav-btn {
  background-color: rgba(255, 255, 255, 0.15);
  border: 1px solid #fff;
  color: white;
  padding: 0.8rem 2rem;
  border-radius: 8px;
  cursor: pointer;
  font-weight: 500;
  transition: background 0.3s ease;
}

.nav-btn:hover {
  background-color: rgba(255, 255, 255, 0.3);
}

/* --- Mobile Specific Styles --- */

/* Mobile Menu Toggle Button (Hamburger) */
.menu-toggle {
  display: none; /* Hidden by default */
  background: none;
  border: none;
  color: white;
  font-size: 2rem;
  cursor: pointer;
  position: absolute; /* Position it within the header */
  left: 1.5rem; /* Adjust as needed */
  top: 50%;
  transform: translateY(-50%);
  z-index: 3; /* Above the overlay */
}

/* Mobile Sliding Menu Container */
.mobile-menu {
  position: fixed;
  top: 0;
  left: 0;
  width: 250px; /* Width of the sliding menu */
  height: 100%;
  background-color: #000000d8; /* Dark background for the menu */
  box-shadow: 2px 0 10px rgba(0, 0, 0, 0.5);
  transform: translateX(-100%); /* Start off-screen to the left */
  transition: transform 0.3s ease-out;
  z-index: 101; /* Higher than backdrop */
  display: flex;
  flex-direction: column;
  padding-top: 4rem; /* Space for close button */
}

.mobile-menu.open {
  transform: translateX(0); /* Slide in */
}

/* Close button inside the mobile menu */
.close-menu-btn {
  position: absolute;
  top: 1rem;
  right: 1.5rem;
  background: none;
  border: none;
  color: white;
  font-size: 2rem;
  cursor: pointer;
}

/* Container for navigation links within the mobile menu */
.mobile-nav-links {
  display: flex;
  flex-direction: column;
  gap: 1rem; /* Space between buttons */
  padding: 1.5rem;
}

/* Styles for buttons within the mobile menu */
.mobile-nav-btn {
  width: 100%; /* Make buttons full width */
  text-align: left; /* Align text to left */
  padding: 1rem 1.5rem;
  background-color: rgba(255, 255, 255, 0.1);
  border: none;
  color: white;
  font-size: 1.1rem;
  border-radius: 4px;
  transition: background 0.3s ease;
}

.mobile-nav-btn:hover {
  background-color: rgba(255, 255, 255, 0.2);
}

/* Mobile Menu Backdrop (dims the rest of the screen) */
.mobile-menu-backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  /* --- CHANGE 2: Reduce opacity of the mobile menu backdrop --- */
  background-color: rgba(0, 0, 0, 0.225); /* Changed from 0.5 to 0.3 */
  z-index: 100; /* Below menu, above content */
}

/* Media Query for Mobile View (screens up to 600px wide) */
@media (max-width: 600px) {
  .modern-header {
    height: 150px;
    background-position: right center;
  }

  .brand-title {
    font-size: 1.5rem;
  }

  .desktop-nav {
    display: none;
  }

  /* Show hamburger icon on mobile */
  .menu-toggle {
    display: block;
  }

  /*
   * The .overlay remains to dim the header background.
   * Its flex properties might need adjustment if you have other
   * elements (like a brand title/logo) within it that need to
   * be repositioned relative to the menu-toggle.
   * For this setup, the overlay's centering will just apply
   * to potentially an empty div or a centered title.
   */
}
